@import url(https://fonts.googleapis.com/css2?family=Mulish&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Mulish', sans-serif;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.navbar{
  background-color: #fff !important;
}
.navbar-brand{
  font-size: 1.9rem !important;
  color: #565387 !important;
}
.navbar a{
  font-size: 1.3rem;
  font-weight: 700;
  text-transform: capitalize;
}
.navbar a:hover{
  color: #3498db !important;
}
.menu_active{
  font-weight: bold;
  border-bottom: 1px solid #565387;
}
#header{
  width: 100%;
  height: 80vh;
  margin-top: 70px;
}
#header h1{
  margin: 0 0 10p 0;
  font-size: 48px;
  font-weight: 700;
  color: black;
}
#header .brand_name{
  color: #3498db;
}
#header h2{
  color: #484848;
  font-size: 24px;
}
#header .btn_get_started{
  font-weight: 600;
  font-size: 16px;
  letter-spacing: 1px;
  display: inline-block;
  padding: 10px 30px;
  border-radius: 50px;
  transition: 0.5s;
  color: #3498db;
  border: 2px solid #3498db;
  text-decoration: none;
}
#header .btn_get_started:hover{
  background: #3498db;
  color: #fff;
}
.header_img{
  text-align: right;
}
#header .animiated{
  animation: up down 2s ease-in-out infinite alternate-reverse both;
}

@media(max-width:991px){
  #header{
    height: 80vh;
    text-align: center;
  }
  #header .header_img{
    text-align: center;
  }
  #header .header_img img{
    width: 60%;
  }
}

@media(max-width:768px){
  #header{
    margin-top: 20px;
  }
  #header h1{
    font-size: 28px;
  }
  #header h2{
    font-size: 18px;
    margin-bottom: 30px;
  }
  
  #header .header_img img{
    width: 70%;
  }
}

